ul {
  list-style-type: lower-roman;
  font-size: 20px;
}

.container {
  width: 500px;
  margin: 0 auto;
  margin-top: 60px;
}

.heading {
  text-align: center;
  margin-bottom: 20px;
}

.button-1 {
  width: 100px;
  height: 80px;
  border: 0;
  outline: none;
  font-size: 3rem;
  font-weight: 900;
  padding: 1rem;
  margin: 0 auto;
  border-radius: 8px;
  cursor: pointer;
  transition: all 600ms ease-in-out;
}

.button-1:hover {
  transform: scale(1.3);
  box-shadow: 3px 3px 4px lightblue;
}

.button-2 {
  border: 0;
  padding: 1rem 2rem;
  font-size: 20px;
  font-family: monospace;
  border-radius: 12px;
  margin: 0.7rem;
  outline: none;
  cursor: pointer;
  transition: all 600ms ease-in-out;
}

.button-2:hover {
  transform: scale(1.03);
  box-shadow: 3px 3px 4px lightblue;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 3px 3px 4px lightslategray, -3px -3px 4px lightslategray;
}
